

import Bus from '../eventbus'
import { upImg } from '../assets/js/upimg.js'
import Loading from 'vux/src/components/loading/index.vue'

export default {
name: 'upimg',
components: {
Loading
},
props: ['upnum'],
data () {
return {
picshowList: [],
imgnum: 1,  // 图片数量（父组件传）
maxnum: false,
toastext: '',
toasttime: 800,
toastwidth: 'auto',
showToast: false,
toastP: 'top',
showLoading: false,
loadText: ''
}
},
created () {

this.imgnum = this.upnum;

},
mounted () {
let vm = this;
console.log('upimg: ',upImg);
},
methods: {

toastFn (toastP, text) {
this.toastP = toastP
this.showToast = true
this.toastext = text
},

upfile (e) {
let vm = this;
let _this = e.currentTarget;
console.log('this.upnum: ',vm.upnum);
console.log('vm.picshowList.length: ',vm.picshowList.length);
console.log('读取中...');

if(vm.picshowList.length+1 == vm.imgnum) {
vm.maxnum = true;
}
if(vm.picshowList.length == vm.imgnum){
vm.toastFn('top', `最多只能传${vm.upnum}张`);
console.log(`最多只能传${vm.upnum}张`);

}else{

vm.loadText = '读取中...';
vm.showLoading = true;

}

upImg( _this, function(){

console.log('图片压缩中...');
vm.loadText = '图片压缩中...';
vm.showLoading = true;

}, function(res){



if(res.err){    // 图片大于6m
vm.toastwidth = '80vw';
vm.toasttime = 1500,
vm.toastFn('top', res.err);
}
if(res.src){
vm.picshowList.push({
binsrc: res.binsrc,
src: res.src,
time: new Date().getTime()
})
vm.upbus();
setTimeout(() => {
vm.showLoading = false;
},300)
}
})
setTimeout(function() {
if(vm.showLoading){
vm.showLoading = false;
}
},3000);

},

delpic (img) {
let vm = this;
img = img.currentTarget;
let time = img.getAttribute('time');
let imgPar = img.parentNode;
for(var i=0; i<vm.picshowList.length; i++){
if(vm.picshowList[i].time == time){


vm.picshowList.splice(i, 1);
vm.upbus();
}
}
if(vm.picshowList.length < vm.upnum){
vm.maxnum = false;
}

},
upbus () {
Bus.$emit('upimg', this.picshowList);
}
},
beforeDestroy () {
this.upbus();
},
destroyed () {

Bus.$off('upimg', this.upbus);
}
}



