import Bus from '../eventbus'
export default {
  name: 'cc',
  // props 接收来自父亲的值
  props: ['time'],
  data () {
    return {
      msg: 'Welcome to cc',
      // 将来自父亲的值保存下来
      cctime: this.time,
      tocc: '嘿嘿cc发出的数据'
    }
  },
  methods: {
    // 传值给爸爸组件
    uo2baba () {
      this.$emit('cc-son', '10000元');
    },
    // 传数据给 bbb (eventbus)
    tobbb () {
      Bus.$emit('fromcc', this.tocc);
    }
  },
  beforeDestroy () {
    // 这个时候接收的组件已经created了，可以监听到数据
    this.tobbb();

  },
  destroyed () {
    // 注销事件（防止多次触发，新旧组件都要）
    Bus.$off('fromcc', this.tocc);
  }
} 




