import Bus from '../eventbus'
import upimg from '@/components/upimg'
import cc from '@/Components/cc'

export default {
  name: 'aac',
  components: {
    cc,
    upimg
  },
  data () {
    return {
      msg: 'Welcome to aac',
      time: '来自aac ' + new Date(),
      name: 'haha',
      upnum: 3, // 图片数量
    }
  },
  methods: {
    // 后退
    back () {
      history.back();
    },
    // 接收子组件 cc emit来的数据
    receiveFromCc (data) {
      console.log('ccData: ',data);
    },
    getupimg (img) {
      // 接收的img
      console.log('img: ',img)
    }
  },
  created () {
    // console.log('router: ',this.$router);
    // 接收upimg组件 $emit的upimg数据
    Bus.$on('upimg', this.getupimg);
  },
  destroyed (){
    // 注销事件
    Bus.$off('upimg',this.getupimg);
  }
}



